<template>
  <div id="app">
    <h1>TodoList</h1>
    <!-- 用户输入内容 -->
    <input type="text" @keydown.enter="inputHandle" :value="inputValue" />
    <!-- 未完成 -->
    <h1>未完成事件</h1>
    <div class="not-finished">
      <todo-list
        :props-arr="notFinishedArr"
        @com-event="changeHandle"
      ></todo-list>
    </div>

    <!-- 已经完成 -->
    <h1>已经完成事件</h1>
    <div class="finished">
      <todo-list :props-arr="finishedArr" @com-event="changeHandle"></todo-list>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      // 初始值(响应值)
      inputValue: "",
      // 未完成的事情
      notFinishedArr: [
        {
          id: 1,
          thing: "敲代码",
          isChecked: false,
        },
        {
          id: 2,
          thing: "整理笔记",
          isChecked: false,
        },
      ],
      //  已完成的事情
      finishedArr: [
        {
          id: 3,
          thing: "脑袋放空, 进入提前放假模式",
          isChecked: true,
        },
      ],
    };
  },
  methods: {
    // input框输入内容处理函数
    inputHandle($event) {
      // 将input的值赋给响应值
      this.inputValue = $event.target.value;
      // 添加到未完成事件的数组中
      this.notFinishedArr.push({
        id: Date.now(),
        thing: this.inputValue,
        isChecked: false,
      });
      // 清空input值
      this.inputValue = "";
    },
    // 未完成的事件
    changeHandle(item, index) {
      item.isChecked = !item.isChecked;
      // 如果isChecked为true, 就意味着已完成
      if (item.isChecked) {
        // 推进已完成的数组中
        this.finishedArr.push(item);
        // 删除当前在未完成的数组对应成员
        this.notFinishedArr.splice(index, 1);
      } else {
        // 推进未完成的数组中
        this.notFinishedArr.push(item);
        // 删除当前在已完成的数组对应成员
        this.finishedArr.splice(index, 1);
      }
    },
  },
};
</script>

